div#competition-project-content-container {
    div.project-overview {
        img {
            width: 70%;
        }
    }

    div.project-ideation {
        div.image-next-to-container:nth-of-type(1) {
            @media (max-width: $medium-small) {
              grid-template-columns: 1fr;
            }
            
            div.image-container {
                img {
                    height: 20vw;
                }
            }
        }
        
        div.image-next-to-container:nth-of-type(2) {
            grid-template-columns: auto auto;
            
            @media (max-width: $medium-small) {
              grid-template-columns: 1fr;
            }
        }
    }

    div.project-design {
        div.image-next-to-container:nth-of-type(1) {
            @media (max-width: $medium-small) {
              grid-template-columns: 1fr;
            }

            img {
                // width: 100%;
                height: 40vw;
            }
        }
    }

    div.project-fabrication {
        video {
            width: 60%;
        }

        img:nth-of-type(1) {
            // width: 20vw;
        }

        div.image-next-to-container:nth-of-type(1) {
            @media (max-width: $medium-small) {
              grid-template-columns: 1fr;
            }
            
            img {
                height: 20vw;
            }
        }
    }

    div.project-programming {
        iframe {
            width: 70%;
        }
    }

    div.project-results {
        iframe {
            width: 70%;
        }
    }
}